order\n    background-color: $background-color;\n    border: 2px solid $border-color;\n    border-radius: 50%;\n    height: 1.5rem;\n    width: 1.5rem;\n    cursor: pointer;\n\n    position: absolute;\n    left: 0;\n    top: 0;\n  }\n  label:hover {\n    filter: brightness(1.1);\n  }\n  label::after {\n    // the tick\n    content: \"\";\n    border: 2px solid $tick-color;\n    border-top: none;\n    border-right: none;\n    transform: rotate(-45deg);\n    height: 6px;\n    width: 12px;\n    opacity: 0; // the tick is hidden by default\n\n    position: absolute;\n    top: 5px;\n    left: 3.5px;\n  }\n  input[type=\"checkbox\"] {\n    visibility: hidden; // hides the default checkbox\n  }\n  input[type=\"checkbox\"]:checked + label {\n    background-color: $checked-background-color;\n    border-color: $checked-background-color; // optionally, checked border can have different color\n  }\n  input[type=\"checkbox\"]:checked + label::after {\n    opacity: 1; // shows the tick when checked\n  }\n}\n\nbody {\n  font-family: $font-stack;\n  color: $font-color-primary;\n\n  height: 100vh;\n  width: 100vw;\n\n  display: flex;\n  flex-direction: column;\n\n  background-color: $color-primary;\n}\n\n.modalWrapper {\n  display: none; // hidden by default\n  position: fixed;\n  z-index: 1; // sit on top\n  left: 0;\n  right: 0;\n  width: 100%;\n  height: 100%;\n  //overflow: auto; // enable scroll if needed\n  background-color: rgba(0, 0, 0, 0.4);\n\n  justify-content: center;\n  align-items: center;\n\n  form {\n    background-color: $color-primary;\n    padding: 1rem;\n    border-radius: 5px;\n\n    display: grid;\n    grid-template-rows: 1.5rem repeat(5, 3rem);\n    gap: 0.5rem;\n    align-items: stretch;\n\n    h2 {\n      justify-self: center;\n      font-size: 1.5rem;\n    }\n\n    label {\n      color: rgb(200, 200, 200);\n    }\n\n    input#title,\n    input#author,\n    input[type=\"number\"] {\n      display: block;\n      border: none;\n      outline: none;\n      border-radius: 5px;\n      padding: 0 7px;\n\n      background-color: $color-secondary;\n\n      font: $font-stack;\n      height: 1.8rem;\n      font-size: 0.9rem;\n      color: $font-color-primary;\n      width: 15rem;\n\n      -moz-appearance: textfield; // remove number arrows\n    }\n\n    // remove number arrows\n    input::-webkit-outer-spin-button,\n    input::-webkit-inner-spin-button {\n      -webkit-appearance: none;\n    }\n\n    .read {\n      align-self: center;\n      display: flex;\n      justify-content: space-around;\n      align-items: center;\n\n      .round {\n        height: 1.5rem;\n        @include roundCheckbox(\n          $color-secondary,\n          $color-primary,\n          $color-primary,\n          $font-color-secondary\n        );\n      }\n    }\n\n    button {\n      @extend %mainButtonsShared;\n      border-radius: 10px;\n    }\n  }\n}\n\nheader {\n  flex: 0 1 auto;\n\n  padding: 0.3em 8%;\n\n  background-color: $color-dark;\n  box-shadow: 0 0 5px 0px rgba(0, 0, 0, 0.7);\n\n  display: flex;\n  justify-content: space-between;\n\n  .profile {\n    display: flex;\n    align-items: center;\n    gap: 1.5rem;\n\n    img {\n      height: 2rem;\n      border-radius: 50%;\n    }\n\n    .signIn,\n    .signOut {\n      color: $font-color-primary;\n      font-size: 0.9rem;\n      font-weight: 500;\n\n      &:hover {\n        filter: brightness(1.2);\n      }\n    }\n  }\n}\n\nmain {\n  flex: 1 1 auto;\n\n  display: flex;\n  flex-direction: column;\n\n  .newBookBtn {\n    @extend %mainButtonsShared;\n    flex: 0 1 auto;\n    align-self: center;\n\n    display: flex;\n    align-items: center;\n    border-radius: 2em;\n    padding-right: 0.9em;\n    margin: 1.5em;\n\n    svg {\n      height: 1.5rem;\n    }\n  }\n\n  .gridWrapper {\n    flex: 1 1 auto;\n\n    display: grid;\n    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));\n    grid-template-rows: repeat(auto-fill, minmax(300px, 1fr));\n    grid-auto-rows: 1fr;\n    gap: 1rem;\n    padding: 0 1rem;\n\n    .bookCard {\n      padding: 1rem;\n\n      border-radius: 5px;\n      background-color: $color-secondary;\n\n      box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.3);\n\n      display: grid;\n      justify-items: stretch;\n      align-items: center;\n      text-align: center;\n      gap: 1em;\n\n      grid-template-rows: repeat(4, 1fr);\n\n      p:first-child {\n        font-size: 1.5rem;\n        font-weight: 600;\n      }\n\n      .buttonsWrapper {\n        display: flex;\n        justify-content: flex-end;\n        align-items: center;\n        gap: 15%;\n\n        .round {\n          height: 1.5rem;\n          @include roundCheckbox(\n            $font-color-primary,\n            $color-secondary,\n            $color-secondary,\n            $font-color-primary\n          );\n        }\n\n        button {\n          border: none;\n          outline: none;\n          background: none;\n\n          color: $font-color-primary;\n          cursor: pointer;\n\n          svg {\n            height: 1.5rem;\n          }\n          svg:hover {\n            filter: brightness(1.1);\n          }\n        }\n      }\n    }\n  }\n}\n\nfooter {\n  flex: 0 1 auto;\n\n  text-align: center;\n  padding: 0.8rem;\n  font-size: 0.8rem;\n\n  a {\n    text-decoration: none;\n    color: $font-color-secondary;\n  }\n}\n"],"sourceRoot":""}]);
